import React from 'react';

import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Box } from '@mui/material';

import { Link } from 'react-router-dom';

import HomeLayout from './HomeLayout';
import Products from './../products/products'

const products = [
  {
    title: 'Torta merengon',
    img: '/products/product2.png',
    desc: '*Para endulzar su da las clsicas fresas con crema con el toque mgico QChimbita!* Postre con fruta de tu eleccin Kiwi, fresa, melocotn, mango, maracuy o banano, con crema de la casa y salsa de tu eleccin. ',
    uuid: 'd239dn2en29fn29fn29',
    price: '7000'
  },
  {
    title: 'Merengon doble durazno"',
    img: '/products/product3.png',
    desc: 'Delicioso Merengn de Durazno con doble capa de crema de la casa, doble capa de merengue y salsa de tu eleccin. ',
    uuid: 'd239dn2en29fn29fn29',
    price: '11000'
  },
  {
    title: 'Torta merengon"',
    img: '/products/product4.png',
    desc: 'Torta de 1000 gramos acompaada de Capa de merengue, Mora, Guanbana, Fresa, Kiwi, Durazno, Crema de la casa y Salsa de tu eleccin.  ',
    uuid: 'd239dn2en29fn29fn29',
    price: '35000'
  },
  {
    title: 'Merengn Doble Fresa',
    img: '/products/product1.png',
    desc: 'Te encantan las fresas? Prueba el exquisito Merengn con doble capa de merengue, doble capa de crema de la casa, Fresas fresca y salsa de tu eleccin. ',
    uuid: 'asdasidnasidna',
    price: '20000'
  }
]

const index = () => {

  const productEnd = products[products.length - 1];

  return (
    <div>
      <HomeLayout
      sxBackground={{
        backgroundImage: `url(${productEnd["img"]})`,
        backgroundColor: '#7fc7d9', // Average color of the background image.
        backgroundPosition: 'center',
      }}>

      <Typography color="inherit" align="center" variant="h2" marked="center">
        {productEnd["title"]}
      </Typography>
      <Typography
      color="inherit"
      align="center"
      variant="h5"
      sx={{ mb: 4, mt: { sx: 4, sm: 10 } }}>
        {productEnd["desc"]}
      </Typography>
      <Button
      color="primary"
      variant="contained"
      size="large"
      component="a"
      href="/menu "
      sx={{ minWidth: 200 }}>
          <Typography color="white">Ver productos</Typography>
      </Button>
      </HomeLayout>

      <Products data={products} />

      <Box sx={{ bgcolor: '#ffece5', pt: 3, pl: 3, pb: 3, display: 'flex', flexWrap: 'wrap'}}>
        <Button sx={{ m: 'auto', bgcolor: '#EE9B92', color: 'white'}}> <Link to="/menu" style={{ textDecoration: 'none', border: 'none', color: 'white' }}> Ver productos </Link></Button>
      </Box>  
    </div>
  );
}

export default index;