import * as React from 'react';

import { Typography, Container, Grid, Button, Avatar } from "@mui/material";

import InstagramIcon from '@mui/icons-material/Instagram';

const Footer = () => {
    return (
        <Typography component="footer" sx={{ display: 'flex', bgcolor: 'secondary.light' }}>
            <Container sx={{ my: 8, display: 'flex' }}>
                <Grid container spacing={5} sx={{display: 'flex', flexDirection: 'column'}}>

                    <Avatar variant="square" alt="Remy Sharp" src="/logo.png" sx={{ width: 190, height: 150, display: { xs: 'flex', md: 'flex' }, m: 'auto'}}/>
                    <br />
                    <InstagramIcon  sx={{color: 'white', m: 'auto', fontSize: 40}}/>
                    <br />
                    <Button href="https://qchimbita.netlify.app/" sx={{color: 'white', m: 'auto'}}>
                        {' '} Q'CHIMBITA {new Date().getFullYear()}
                    </Button>
           
                </Grid>
            </Container>
        </Typography>
    );
}

export default Footer;