
import * as React from 'react';

import { Link } from 'react-router-dom';

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import HomeIcon from '@mui/icons-material/Home';
import FmdGoodOutlinedIcon from '@mui/icons-material/FmdGoodOutlined';
import ShoppingBagOutlinedIcon from '@mui/icons-material/ShoppingBagOutlined';
import ManageAccountsOutlinedIcon from '@mui/icons-material/ManageAccountsOutlined';
import RestaurantMenuOutlinedIcon from '@mui/icons-material/RestaurantMenuOutlined';
import GroupsTwoToneIcon from '@mui/icons-material/GroupsTwoTone';

const pages = ['Inicio', 'Men', 'Nosotros', 'Ubicacin', 'Carrito', 'Mi cuenta']

const settings = ['Cuenta', 'Pedidos', 'Cerrar sesion'];

const ResponsiveAppBar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <AppBar position="fixed">
        <Container maxWidth="xl">
            <Toolbar disableGutters>

                <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                    <IconButton 
                    size="large" 
                    aria-label="account of current user" 
                    aria-controls="menu-appbar" 
                    aria-haspopup="true" 
                    onClick={handleOpenNavMenu} 
                    color="inherit">
                        <MenuIcon />
                    </IconButton>
                    <Menu 
                    id="menu-appbar" 
                    anchorEl={anchorElNav} 
                    anchorOrigin={{
                        vertical: 'bottom', 
                        horizontal: 'left'
                    }} 
                    keepMounted 
                    transformOrigin={{
                        vertical: 'top', 
                        horizontal: 'left'
                    }}
                    open={Boolean(anchorElNav)}
                    onClose={handleCloseNavMenu}
                    sx={{
                        display: { 
                            xs: 'block', 
                            md: 'none' 
                        },
                    }}>
                        {pages.map((page) => (
                            <MenuItem key={page} onClick={handleCloseNavMenu}>
                                <Typography textAlign="center">{page}</Typography>
                            </MenuItem>
                        ))}
                    </Menu>
                </Box>

                <Avatar alt="Remy Sharp" src="/logo.png" sx={{ width: 56, height: 56, display: { xs: 'flex', md: 'none' }, mr: 1 }}/>

                <Box sx={{ flexGrow: 1 }} />

                <Box sx={{ display: { xs: 'none', md: 'flex' } }}>

                    <Link to="/ " style={{ textDecoration: 'none', border: 'none' }}>
                        <Button
                        key="Inicio"
                        onClick={handleCloseNavMenu}
                        startIcon={<HomeIcon/>}
                        variant="outlined"
                        sx={{ color: 'white' }}>
                            Inicio
                        </Button>
                    </Link>

                    <Link to="/menu" style={{ textDecoration: 'none', border: 'none' }}>
                        <Button
                        key="Menu"
                        onClick={handleCloseNavMenu}
                        startIcon={<RestaurantMenuOutlinedIcon/>}
                        variant="outlined"
                        sx={{ color: 'white' }}>
                            Menu
                        </Button>
                    </Link>

                    <Link to="/nosotros" style={{ textDecoration: 'none', border: 'none' }}>
                        <Button
                        key="Nosotros"
                        onClick={handleCloseNavMenu}
                        startIcon={<GroupsTwoToneIcon/>}
                        variant="outlined"
                        sx={{ color: 'white' }}>
                            Nosotros
                        </Button>
                    </Link>
                </Box>

                
                <Link to="/ " style={{ textDecoration: 'none', border: 'none' }}>
                    <Avatar variant="square" alt="Remy Sharp" src="/logo.png" sx={{ width: 90, height: 50, display: { xs: 'none', md: 'flex' }, mr: 1 }}/>
                </Link>

                <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                    <Link to="/ubicacion" style={{ textDecoration: 'none', border: 'none' }}>
                        <Button
                        key="Ubicacin"
                        onClick={handleCloseNavMenu}
                        startIcon={<FmdGoodOutlinedIcon/>}
                        variant="outlined"
                        sx={{ color: 'white' }}>
                            Ubicacin
                        </Button>
                    </Link>

                    <Link to="/carrito" style={{ textDecoration: 'none', border: 'none' }}>
                        <Button
                        key="Carrito"
                        onClick={handleCloseNavMenu}
                        startIcon={<ShoppingBagOutlinedIcon/>}
                        variant="outlined"
                        sx={{ color: 'white' }}>
                            Carrito
                        </Button>
                    
                    </Link>
                        

                    <Tooltip title="Open settings">
                        <Button
                        key="Mi cuenta" 
                        onClick={handleOpenUserMenu} 
                        variant="outlined" 
                        startIcon={<ManageAccountsOutlinedIcon/>}
                        sx={{ color: 'white' }}>
                            Mi cuenta
                        </Button>
                    </Tooltip>
                    <Menu
                    sx={{ mt: '45px' }}
                    id="menu-appbar"
                    anchorEl={anchorElUser}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    keepMounted
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    open={Boolean(anchorElUser)}
                    onClose={handleCloseUserMenu}>
                        {settings.map((setting) => (
                            <MenuItem key={setting} onClick={handleCloseUserMenu}>
                                <Typography textAlign="center">{setting}</Typography>
                            </MenuItem>
                        ))}
                    </Menu>
                </Box>
            </Toolbar>
        </Container>
    </AppBar>
  );
};

export default ResponsiveAppBar;
