import * as React from 'react';

import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import { Grid } from '@mui/material';

const Products = (props) => {
    return (
        <Grid container sx={{ bgcolor: '#ffece5', mt: props.sx ? props.sx.mt : 0, display: 'flex', flexDirection: 'column' }}>
            <Grid sx={{mt: 4, mb: 4, ml: 'auto', mr: 'auto'}}>
                <Typography sx={{fontSize: 40}}>Nuestro men</Typography>
            </Grid>

            <Grid sx={{display: 'flex', flexWrap: 'wrap'}}>
                {props.data.map( (value) => {
                    return (
                        <Card sx={{ maxWidth: 345, ml: 'auto', mb: 2, mr: 'auto', mt: 2 }}>
                            <CardHeader title={value.title} sx={{textAlign: 'center'}} />

                            <CardMedia component="img" image={value.img} alt="qchimbitaproduct" sx={{maxHeight: 340, minHeight: 340}}/>
                    
                            <CardContent>
                                <Typography variant="body2" color="text.secondary">
                                    {value.desc}
                                </Typography>
                            </CardContent>
                            <CardActions disableSpacing>
                                <Typography variant="body" color="text.primary" sx={{fontWeight: 'bold'}}>
                                    ${value.price}
                                </Typography>

                                <IconButton aria-label="add to favorites" sx={{ml:'auto'}}>
                                    <AddShoppingCartIcon />
                                </IconButton>
                            </CardActions>
                        </Card>
                    )
                })}
            </Grid>
        </Grid>
    )
}

export default Products;