
import Typography from '@mui/material/Typography';
import { Grid, Box, Chip } from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import AccessTimeFilledIcon from '@mui/icons-material/AccessTimeFilled';
import LocationCityIcon from '@mui/icons-material/LocationCity';

import Map from '../../components/map/map';

import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init({
    duration: 1200,
});

const Location = () => {

    return (
        <Grid container sx={{ bgcolor: '#ffece5', mt: 8, display: 'flex', flexDirection: 'column'  }}>
            <Grid sx={{mt: 4, mb: 4, ml: 'auto', mr: 'auto'}}>
                <Typography sx={{fontSize: 40}}>Bello</Typography>
            </Grid>

            <Grid sx={{ display: 'flex', flexDirection: 'column', pl: 2, pr: 2 }} container >
                <div data-aos="fade-up" data-aos-offset="200" data-aos-delay="50" data-aos-duration="1000" data-aos-mirror="true" data-aos-once="false"> 
                    <Box sx={{ pt: 2, pb: 2, pl: 57 }}>
                        <img src='/store/store.jpg' srcSet="/store/store.jpg" alt="firstadmin" loading="lazy" />
                    </Box>

                    <Grid sx={{ display: 'flex', flexWrap: 'wrap' }}>
                        <Typography sx={{ m: 'auto' }}>
                            <Chip icon={<LocationOnIcon />} label="Cr 53a #52a - 13" />
                        </Typography>

                        <Typography sx={{ m: 'auto' }}>
                            <Chip icon={<LocalPhoneIcon />} label="2042559" />
                        </Typography>

                        <Typography sx={{ m: 'auto' }}>
                            <Chip icon={<PhoneAndroidIcon />} label="3042589518" />
                        </Typography>

                        <Typography sx={{ m: 'auto' }}>
                            <Chip icon={<AccessTimeFilledIcon />} label="Lunes a sbado 8:00 am a 6:00 pm, domingo y festivos no prestamos servicio" />
                        </Typography>

                        <Typography sx={{ m: 'auto' }}>
                            <Chip icon={<LocationCityIcon />} label="Bello, Antioquia, Colombia" />
                        </Typography>
                    </Grid>

                    <br />

                    <Grid sx={{ width: 900, height: 300, ml: 'auto', mr: 'auto', mb: 5 }} >
                        <Map />
                    </Grid>
                </div>

            </Grid>
            
        </Grid>
    )

}

export default Location;