import React from 'react';

import { Map, Source, Layer } from 'react-map-gl';

const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1IjoiY2RiZXJyaW8iLCJhIjoiY2w1NnZpMTd0MW1ldzNkcXEzNm95eWNiOCJ9.AXB_foWE3slZGJQ-A3XCFw';

const layerStyle = {
    id: 'point',
    type: 'circle',
    paint: {
      'circle-radius': 10,
      'circle-color': '#007cbf'
    }
};

const geojson = {
    type: 'FeatureCollection',
    features: [
      {type: 'Feature', geometry: {type: 'Point', coordinates: [-75.5617859, 6.3361665]}}
    ]
};

const Maps = () => {

  return (
    <Map
    initialViewState={{
      longitude:  -75.5617859,
      latitude: 6.3361665,
      zoom: 15
    }}
    style={{width: '100%', height: '100%'}}
    mapStyle="mapbox://styles/mapbox/streets-v9"
    attributionControl={false}
    mapboxAccessToken={MAPBOX_ACCESS_TOKEN}
    >
        <Source id="my-data" type="geojson" data={geojson}>
            <Layer {...layerStyle} />
        </Source>
    </Map>
  );
}

export default Maps;